.mobile-input-field .fb-field-item {
  display: none;
}
.open-code-help {
  @apply text-fb-blue;
  text-decoration: underline;
}
.multifactor-auth-verify button.verify-button {
  width: 100%!important;
}

.verification-input-field .verification-label {
  @apply ml-4 mb-2 text-fb-black;
  display: block;
}
.multifactor-auth-verify .mobile-input-field input{
  height: 54px;
}
.multifactor-auth-verify h2 {
  font-weight: 500!important;
}
.multifactor-auth-verify input[type=tel] {
  border-bottom: solid 1px rgb(190, 198, 209);
}
.vi__wrapper input {
  caret-color: transparent;
}
.vi__container {
  width: 290px;
  height: 48px;
}
.vi__character {
  background-color: rgb(246, 246, 246);
  border: none;
  border-top-left-radius: 0.75rem;
  border-top-right-radius: 0.75rem;
  border-bottom: solid 1px rgb(190, 198, 209);
  font-size: 18px;
}
.btn-edit-mobile {
  margin-left: 1rem;
  margin-top: 0.5rem;
  text-decoration: underline;
  font-weight: 300;
  font-size: 18px;
  color: #0F4B91;
}
