
@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import "@flybuys/ui-kit/dist/vendor/rsuite-4.10.9.css";
@import "@flybuys/ui-kit/dist/index.css";
@import "@flybuys/ui-kit/dist/icon.css";
@import "@flybuys/ui-kit/dist/base.css";
@import "@flybuys/ui-kit/dist/body.css";
@import "@flybuys/ui-kit/dist/button.css";
@import "@flybuys/ui-kit/dist/textfield.css";
@import "@flybuys/ui-kit/dist/modal.css"; 
@import "@flybuys/ui-kit/dist/password-rules.css";
@import "@flybuys/ui-kit/dist/accordion.css";
@import 'tailwindcss/utilities';

@import "./typography.css";

/* JS-Library Style Overrides */
body {
  @apply font-public-sans-rg bg-fb-offWhite;
}

body strong,
body b {
  @apply font-public-sans-sb;
}
.app-background {
  @apply md:bg-gray-100	
}
html,
.text-black {
  color: #263038;
}

.fb-section-container .fb-section-header a > .fb-arrow-right,
.fb-section-container .fb-section-header button > .fb-arrow-right {
  display: block;
}

/* Section */
.fb-section-container {
  margin: 35px auto;
}

/* Partner Cards */
.fb-partner-card-text-content-title {
  @apply text-sm;
  font-family: 'PublicSansBd';
}
.fb-partner-card-text-content-desc {
  @apply text-sm;
  font-family: 'PublicSansRg';
}

/* Error */
.fb-pageintro .fb-pageintro-heading {
  font-family: 'PublicSansBd';
}
.fb-pageintro div > span,
.fb-pageintro div > span > a {
  @apply text-sm;
  font-family: 'PublicSansRg';
}

/* Explore Cards */
.fb-explore-card-copy span {
  @apply text-sm;
  font-family: 'PublicSansRg';
}
.fb-explore-card-copy span:first-child {
  font-family: 'PublicSansBd';
}

#rewards-router {
  display: block;
}
@screen md {
  body {
    @apply text-bu-fb-body-base-md;
  }
}

@screen lg {
  .fb-section-container {
    margin: 60px auto;
  }

  .fb-section-container .fb-section-body {
    margin-top: 20px;
  }

  .fb-partner-card-text-content-title {
    @apply text-lg;
  }

  .fb-partner-card-text-content-desc {
    @apply text-lg;
  }

  .fb-pageintro div > span,
  .fb-pageintro div > span > a {
    @apply text-lg;
  }

  .fb-explore-card-copy span {
    @apply text-lg;
  }
}

